	const app = getApp()
import uniRate from '@/componets/wyc-rate/uni-rate.vue';
export default {
	data() {
		return {
			t_length: 0,
			rateValue: 5,
			order: {},
			imgUrl: '',
			inputVal: ''
		};
	},
	components: { uniRate },
	onLoad(e) {
		this.imgUrl = this.$helper.imgUrl
		this.type = e.type
		let orderObj = ''
		if(e.type == 'curricula'){
			orderObj = JSON.parse(e.obj)
			this.ordernum = orderObj.ordernum
			this.order = orderObj.curricula
		}else{
			orderObj = JSON.parse(e.obj)
			this.ordernum = orderObj.ordernum
			this.order = orderObj.data
		}
	},
	methods: {
		bindText(e) {
			this.t_length = e.detail.value.length
			this.inputVal = e.detail.value
		},
		rateChange(val){
		  this.rateValue = val.value
		},
		post(){
			if(this.inputVal == ''){
				uni.showToast({
					title: '请输入评价内容',
					icon: 'none'
				})
				return false
			}
			uni.request({
				url: `${this.$helper.requestUrl}user/evaluate`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: this.type,
					ordernum: this.ordernum,
					content: this.inputVal,
					star: this.rateValue
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
						setTimeout(e => {
							uni.navigateBack({
								delta: 1
							})
						}, 2000)
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
